
@media screen and (min-width: 320px) {
html {
    font-size: 42.66667px;
}
}
@media screen and (min-width: 360px) {
html {
    font-size: 48px;
}
}
@media screen and (min-width: 375px) {
html {
    font-size: 50px;
}
}
@media screen and (min-width: 412px) {
html {
    font-size: 54.93333px;
}
}
@media screen and (min-width: 414px) {
html {
    font-size: 55.2px;
}
}
@media screen and (min-width: 768px) {
html {
    font-size: 102.4px;
}
}
html, body, #app {
  width: 100%;
  height: 100%;
}
.play-show-enter-active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.play-show-enter-to {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.play-show-leave-active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.footer-show-enter-active {
  -webkit-transform: translateY(120%);
          transform: translateY(120%);
  opacity: 0;
}
.footer-show-enter-active-to {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}
.footer-show-leave-active {
  -webkit-transform: translateY(120%);
          transform: translateY(120%);
  opacity: 1;
}
.footer-show-leave-active-to {
  -webkit-transform: translateY(120%);
          transform: translateY(120%);
  opacity: 0;
}
.page-view-enter-active, .page-view-leave-active {
  transition: all .5s ease-in-out;
}
.page-view-enter-active {
  opacity: 0;
}
.page-view-enter-to {
  opacity: 1;
}
.page-view-leave-active {
  opacity: 1;
}
.page-view-leave-to {
  opacity: 0;
}



